.t{
    height: 25px;
    width:50px;
    border-radius: 20px;
    border:1px solid #999;
    background-color: rgb(229, 72, 72);
    position:fixed;
    top:10px;
    right:10px;
    z-index:1;
    display: flex;
    /* flex-direction: column; */
    align-items: center;
    justify-content: space-around;
}
.t-icon{
    width: 15px;
    height: 25px;
}
.t-button{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #999;
    position:absolute;
    cursor:pointer;


}

